import { getTypeList } from '@/api/type';
var columns = [{
  dataIndex: 'id',
  title: 'ID'
}, {
  dataIndex: 'name',
  title: '分类名称'
}, {
  dataIndex: 'user.real_name',
  title: '创建用户'
}];
export default {
  data: function data() {
    return {
      columns: columns,
      dataSource: [],
      listLoading: false
    };
  },
  created: function created() {
    this.fetch();
  },
  methods: {
    fetch: function fetch() {
      var _this = this;

      this.listLoading = true;
      getTypeList().then(function (res) {
        if (res.status) {
          _this.dataSource = res.data.data;
          _this.listLoading = false;
        }
      });
    }
  }
};