import request from '@/utils/request';
export function getTypeList(params) {
  return request({
    url: '/api/admin/type',
    method: 'get',
    params: params
  });
}
export function deleteType(id) {
  return request({
    url: '/api/admin/type/' + id,
    method: 'delete'
  });
}
export function getType(id) {
  return request({
    url: '/api/admin/type/' + id,
    method: 'get'
  });
}
export function addType(data) {
  return request({
    url: 'api/admin/type',
    method: 'post',
    data: data
  });
}
export function updateType(id, params) {
  return request({
    url: '/api/admin/type/' + id,
    method: 'put',
    params: params
  });
}